import React, { Component } from 'react';

class Header extends Component {
	render() {
		return (
			<header id="home">
		        <nav id="nav-wrap">
		          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
		          <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>
		          <ul id="nav" className="nav">
		            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
		            <li><a className="smoothscroll" href="#about">About</a></li>
		            <li><a className="smoothscroll" href="#resume">Resume</a></li>
		            <li><a className="smoothscroll" href="#portfolio">Works</a></li>
		            <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
		            <li><a className="smoothscroll" href="#contact">Contact</a></li>
		          </ul> {/* end #nav */}
		        </nav> {/* end #nav-wrap */}
		        <div className="row banner">
		          <div className="banner-text">
		            <h1 className="responsive-headline">I'm Emmanuel Djessou</h1>
		            <h3>I'm a <span>Web</span> and <span>Android</span> developer currently looking
		     		for an opportunity to develop my skills. Let's <a className="smoothscroll" href="#about">start scrolling </a>
		               and learn more <a className="smoothscroll" href="#about"> about me</a>.</h3>
		            <hr />
		            <ul className="social">
		              <li><a href="https://www.facebook.com/edjessou" target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook" /></a></li>
		              <li><a href="https://twitter.com/Manueldo12" target="_blank" rel="noopener noreferrer"><i className="fa fa-twitter" /></a></li>
		        	  <li><a href="https://www.linkedin.com/in/emmanuel-djessou-167a6b115" target="_blank" rel="noopener noreferrer"><i className="fa fa-linkedin" /></a></li>
		            </ul>
		          </div>
		        </div>
		        <p className="scrolldown">
		          <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
		        </p>
		    </header>
		);
	}
}

export default Header;