import React, { Component } from 'react';

class Resume extends Component {
	render() {
		return (
			<section id="resume">
          {/* Education
      ----------------------------------------------- */}
          <div className="row education">
            <div className="three columns header-col">
              <h1><span>Education</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="row item">
                <div className="twelve columns">
                  <h3>Zhejiang Gongshang University</h3>
                  <p className="info">Master in Computer Science and Technology <span>•</span> <em className="date">September 2017 - June 2020</em></p>
                  <p>
                    Zhejiang Gongshang University has a great master program that helped me to improve my skills and knowledge.
                  </p>
                </div>
              </div> {/* item end */}
              <div className="row item">
                <div className="twelve columns">
                  <h3>Bangalore University</h3>
                  <p className="info">B.Sc. Degree in Computer Science <span>•</span> <em className="date">June 2016</em></p>
                  <p>
                    My Bachelor degree helped me to realize what was involved in being a computer scientist. It helped me to learn the basics of programming
                    and to improve my eager to know more about it. Bangalore University is one of the best in Karnataka,India. 
                  </p>
                </div>
              </div> {/* item end */}
            </div> {/* main-col end */}
          </div> {/* End Education */}
          {/* Work
      ----------------------------------------------- */}
          <div className="row work">
            <div className="three columns header-col">
              <h1><span>Work</span></h1>
            </div>
            <div className="nine columns main-col">
              <div className="row item">
                <div className="twelve columns">
                  <h3>Freelancer</h3>
                  <p className="info">Web/Android dev<span>•</span> <em className="date">March 2018 - Present</em></p>
                  <p>
                    Being focused on my studies, I have not really worked for any company yet. Nevertheless, since march 2018 I decided to work
                    as a freelancer most of the time for free.
                  </p>
                </div>
              </div> {/* item end */}
            </div> {/* main-col end */}
          </div> {/* End Work */}
          {/* Skills
      ----------------------------------------------- */}
          <div className="row skill">
            <div className="three columns header-col">
              <h1><span>Skills</span></h1>
            </div>
            <div className="nine columns main-col">
              <p>
                This is a list of the skills I have with the percentage of my knowledge of them. This list is not finite. 
                It is just a list of technologies I use the most. I'm still working to improve in those technologies with lower percentage. 
              </p>
              <div className="bars">
                <ul className="skills">
                  <li><span className="bar-expand photoshop" /><em>PHP</em></li>
                  <li><span className="bar-expand illustrator" /><em>Javascript</em></li>
                  <li><span className="bar-expand wordpress" /><em>JAVA</em></li>
                  <li><span className="bar-expand css" /><em>CSS</em></li>
                  <li><span className="bar-expand html5" /><em>HTML5</em></li>
                  <li><span className="bar-expand jquery" /><em>XML</em></li>
                </ul>
              </div>{/* end skill-bars */}
            </div> {/* main-col end */}
          </div> {/* End skills */}
        </section>

		);
	}
}

export default Resume;