import React, { Component } from 'react';

class Portfolio extends Component {
	render() {
		return (

		<div>	
			<section id="portfolio">
          <div className="row">
            <div className="twelve columns collapsed">
              <h1>Check Out Some of My Works.</h1>
              {/* portfolio-wrapper */}
              <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-01">
                      <img alt="" src="images/portfolio/Original.png" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Hairmuseum</h5>
                          <p>Wordpress app</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> {/* item end */}
               {/*  <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-02">
                      <img alt="" src="images/portfolio/logo.png" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Cycyretail</h5>
                          <p>React app</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> */} {/* item end */} 
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-04">
                      <img id="android1" alt="" src="images/portfolio/android.png" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Church</h5>
                          <p>Android app</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> {/* item end */}
              </div> {/* portfolio-wrapper end */}
            </div> {/* twelve columns end */}
            {/* Modal Popup
	      --------------------------------------------------------------- */}
            <div id="modal-01" className="popup-modal mfp-hide">
              <img className="scale-with-grid" src="images/portfolio/modals/hairmuse.png" alt="" />
              <div className="description-box">
                <h4>Hairmuseum</h4>
                <p>Hairmuseum is an e-commerce website that is made to sell wigs and virgin human hair online.</p>
                <span className="categories"><i className="fa fa-tag" />Branding, Web development</span>
              </div>
              <div className="link-box">
                <a href="https://hairmusuem.site/" target="_blank" rel="noopener noreferrer">View online</a>
                <a href="#portfolio" className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-01 End */}
            <div id="modal-02" className="popup-modal mfp-hide">
              <img className="scale-with-grid" src="images/portfolio/modals/Artboard.png" alt="" />
              <div className="description-box">
                <h4>Cycyretail</h4>
                <p>Cycyretail is an online store that is used to sell several kind of items online. Visit and make your purchase.</p>
                <span className="categories"><i className="fa fa-tag" />Branding, Web Development</span>
              </div>
              <div className="link-box">
                <a href="https://bitto" target="_blank" rel="noopener noreferrer">View online</a>
                <a  href="#portfolio" className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-02 End */}
            <div id="modal-04" className="popup-modal mfp-hide">
              <img id="android2" className="scale-with-grid" src="images/portfolio/modals/android2.png" alt="" />
              <div className="description-box">
                <h4>ChurchApp</h4>
                <p>This is one of the many apps I made with Android studio. Unfortunately, I have no app yet on the play store. I just code them for free.</p>
                <span className="categories"><i className="fa fa-tag" />Branding, Android development</span>
              </div>
              <div className="link-box">
                <a href="#portfolio" className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-03 End */}
          </div> 
        </section> 
        
        </div>
		);
	}
}


export default Portfolio;